@import url("https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;600;700;800&family=Roboto:wght@300;400;500;700;900&display=swap");
* {
  padding: 0;
  margin: 0;
  border: 0;
}

*,
*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

:focus,
:active {
  outline: none;
}

a:focus,
a:active {
  outline: none;
}

nav,
footer,
header,
aside {
  display: block;
}

html,
body {
  height: 100%;
  width: 100%;
  font-size: 100%;
  line-height: 1;
  font-size: 14px;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

input,
button,
textarea {
  font-family: inherit;
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

a,
a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

img {
  vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  font-weight: 400;
}

html {
  scroll-behavior: smooth;
}

* {
  line-height: 130%;
}

body {
  font-size: 15px;
  font-family: "Inter", sans-serif;
  padding-top: 202px;
  transition: all ease 0.5s;
}

.filter {
  filter: grayscale(1);
}

.main-event__image img,
.directions-banner img,
.directions-offer__image img,
.category-slider__image img,
.news-list__image,
.licenses-slider__image img,
.licenses-images__item img,
.services-list__image,
.webinars-banner img,
.webinars-list__image img,
.webinars-list__icon img,
.webinars-offer img,
.sales-list__image img,
.faq-video__block img,
.students-user__image img,
.about-team__image img,
.about-connect__image img,
.documents-list__image img,
.course__bg {
  display: block;
  width: 100%;
  height: 100%;
  -o-object-fit: cover;
  object-fit: cover;
}

input,
input::placeholder,
textarea,
textarea::placeholder,
button,
a {
  font-family: "Roboto", sans-serif;
}

a,
a:hover,
a:active,
a:focus,
button,
input,
button:hover,
button:active,
button:focus,
input:focus,
input:active {
  transition: all ease 0.5s;
}

.container {
  max-width: 1640px;
  width: 100%;
  padding: 0 20px;
  margin: 0 auto;
}

.f-center-jcsb {
