}

.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 4;
  background: #fff;
  box-shadow: 0px 5px 30px rgba(0, 0, 0, 0.05);
}
.header-contacts {
  padding-top: 20px;
  padding-bottom: 18px;
  background: #f7f7f7;
}
.header-contacts__city {
  margin-right: 38px;
}
.header-contacts__city svg {
  margin-right: 5px;
  display: block;
}
.header-contacts__point {
  font-weight: 500;
  font-size: 15px;
  line-height: 18px;
  color: #575757;
}
.header-list {
  justify-content: unset !important;
  margin-left: auto;
}
.header-list__item {
  margin-right: 32px;
}
.header-list__item svg {
  margin-right: 8px;
  display: block;
}
.header-filter {
  padding: 0;
  width: 40px;
  height: 40px;
  border: 1px solid #222;
  border-radius: 8px;
}
.header-filter svg {
  width: 18px;
  height: 18px;
}
.header-filter__active {
  background: #222;
}
.header-filter__active svg path {
  stroke: #fff;
}
.header-wrapper {
  position: relative;
  border-bottom: 1px solid #e4e4e4;
}
.header-wrapper__block {
  width: 100%;
}
.header-wrapper__top {
  padding: 14px 0;
  margin-left: 26px;
}
.header-wrapper .burger-btn {
  display: none;
  z-index: 10;
  position: relative;
  width: 30px;
  height: 20px;
  cursor: pointer;
}
.header-wrapper .burger-btn > span {
  position: absolute;
  background-color: #000000;
  width: 100%;
  height: 3px;
  border-radius: 18px;
  left: 0;
  top: 9px;
  transition: all ease 0.5s;
}
.header-wrapper .burger-btn:after, .header-wrapper .burger-btn:before {
  content: "";
  position: absolute;
  background-color: #000000;
  width: 100%;
  height: 3px;
  border-radius: 18px;
  left: 0;
  transition: all ease 0.5s;
}
.header-wrapper .burger-btn:before {
  top: 20px;
}
.header-wrapper .burger-btn:after {
  bottom: 20px;
}
.header-wrapper .burger-btn_active span {
  opacity: 0;
}
.header-wrapper .burger-btn_active:before {
  transform: rotate(45deg);
  top: 9px;
}
.header-wrapper .burger-btn_active:after {
  transform: rotate(-45deg);
  bottom: 9px;
}
.header-inner__text {
  display: none;
  margin-left: 17px;
  font-weight: 400;
  font-size: 10px;
  line-height: 150%;
  letter-spacing: -0.03em;
  color: #afafaf;
}
.header-logo {
  height: 125px;
  border-right: 1px solid #e4e4e4;
  -ms-flex-negative: 0;
  flex-shrink: 0;
}
.header-logo__link {
  display: block;
}
.header-logo__link img {
  border-style: none;
}
.header-toggle {
  display: none;
}
.header-menu__item {
  margin-right: 24px;
  position: relative;
}
.header-menu__link {
  font-weight: 700;
  font-size: 13px;
  line-height: 16px;
  letter-spacing: 0.05em;
  color: #515151;
}
.header-menu__link:hover {
  color: #fbe808;
}
.header-btns__item {
  margin-right: 11px;
}
.header-btns__item:last-child {
  margin-right: 0;
}
.header-category__link {
  font-weight: 600;
  font-size: 13px;
  line-height: 16px;
  letter-spacing: 0.03em;
  text-transform: uppercase;
  padding: 18px 16.5px;
  padding-bottom: 17px;
  color: #202020;
  display: block;
}
.header-category__link:hover {
  background: #172a6c;
  color: #fff;
}
@media screen and (max-width: 1400px) {
  .header-menu {
    gap: 10px;
  }
  .header-menu__item {
    margin-right: 0;
  }
}
@media screen and (max-width: 1240px) {
  .header-wrapper .container {
    justify-content: space-between;
  }
  .header-wrapper__block {
    background: #f7f7f7;
