    margin-right: 10px;
  }
}
@media screen and (max-width: 768px) {
  .header-contacts__wrap {
    flex-direction: column;
    align-items: baseline;
    gap: 5px;
  }
  .header-contacts__point {
    font-size: 11px;
  }
  .header-list {
    flex-direction: column;
    align-items: baseline;
  }
  .header-inner {
    max-width: 200px;
    max-height: 75px;
  }
  .header-inner__wrap {
    width: 100%;
    height: 100%;
  }
  .header-logo {
    width: 100%;
    height: 100%;
    padding: 10px 0;
  }
  .header-logo__link {
    width: 100%;
  }
  .header-logo__link img {
    width: 100%;
  }
  .header-wrapper__block {
    padding: 0 10px;
    display: flex;
    flex-direction: column-reverse;
  }
  .header-wrapper__top {
    margin-left: 0;
    flex-wrap: wrap;
    justify-content: space-evenly;
    gap: 10px;
    padding: 10px 0;
  }
  .header-wrapper .active {
