.show-youtube {
  display: none;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 5;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.82);
}
.show-youtube_active {
  display: flex;
}
.show-youtube__close {
  position: absolute;
  top: 0;
  right: 0;
  cursor: pointer;
}

.popup {
  z-index: -1;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  transition: all 0.4s ease;
  opacity: 0;
}
.popup_active {
  opacity: 1;
  z-index: 5;
}
.popup-overlay {
  overflow-y: auto;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.7);
}
.popup-body {
  width: fit-content;
  position: absolute;
  top: 150px;
  left: 50%;
  transform: translateX(-50%);
}
.popup-close {
  width: 36px;
  height: 36px;
  background: #ffffff;
  border-radius: 4px;
  cursor: pointer;
  position: absolute;
  right: 0;
  top: -44px;
  transition: all 0.4s ease;
}
.popup-close:hover {
  background: #f52c2c;
}
.popup-close svg {
  display: block;
}
.popup-close svg path {
  transition: all 0.4s ease;
}
.popup .connect-form {
  margin-top: 0 !important;
  max-width: 443px;
  width: 100%;
  color: #fff;
  flex-shrink: 0;
  text-align: center;
  border-radius: 22px;
  background: #172a6c;
  padding: 27px 38px 32px 38px;
}
.popup .connect-form__title {
  font-weight: 400;
  font-size: 26px;
  line-height: 31px;
  text-align: center;
  letter-spacing: -0.03em;
}
.popup .connect-form__text {
  margin-top: 8px;
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
  text-align: center;
  letter-spacing: -0.03em;
  color: rgba(255, 255, 255, 0.8);
}
.popup .connect-form__wrapper {
  margin-top: 24px;
}
.popup .connect-form__item {
  margin-bottom: 12px;
}
.popup .connect-form__item input, .popup .connect-form__item select, .popup .connect-form__item textarea {
  display: block;
  width: 100%;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 16px;
  padding: 22px;
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
  color: #fff;
  border: 0;
}
.popup .connect-form_select {
  position: relative;
}
.popup .connect-form_select::after {
  content: url(../img/drop-arrow.svg);
  position: absolute;
  top: 50%;
  -webkit-transform: translateY(-50%);
  transform: translateY(-50%);
  display: block;
  right: 20px;
}
.popup .connect-form select {
  -moz-appearance: none;
  -webkit-appearance: none;
  cursor: pointer;
}
.popup .connect-form select option {
  background: #fff;
  color: #000;
  cursor: pointer;
}
.popup__submit {
  width: 100%;
  padding: 24px 0;
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
  font-family: "Roboto", sans-serif;
  color: #212121;
  text-transform: none;
}
.popup .accept-politic {
  margin-top: 24px;
}
.popup .accept-politic__label {
  cursor: pointer;
}
.popup .accept-politic__check {
  width: 16px;
  height: 16px;
  border: 1px solid #ffffff;
  border-radius: 4px;
  -ms-flex-negative: 0;
  flex-shrink: 0;
  margin-right: 7px;
  -webkit-transform: translateY(1px);
  transform: translateY(1px);
}
.popup .accept-politic__check svg {
  transition: all 0.4s ease;
  display: block;
  opacity: 0;
}
.popup .accept-politic__check_active svg {
  opacity: 1;
}
.popup .accept-politic__text {
  text-align: left;
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
  letter-spacing: -0.03em;
  color: rgba(255, 255, 255, 0.8);
}
@media screen and (max-width: 768px) {
  .popup .connect-form {
    display: block;
  }
}